import React from 'react';
import ReactDOM from 'react-dom';

import WorkmediaList from './WorkmediaList';

const isShort = document.getElementById('workmedia-list').classList.contains('short');

ReactDOM.render(
  <React.StrictMode>
    <WorkmediaList isShort={isShort}/>
  </React.StrictMode>,
  document.getElementById('workmedia-list')
);
