import { useEffect, useState } from "react";
import { useCollection } from "./hooks/useCollection";
import Workmedia from './Workmedia';

const TrdsWorkmediaListStyle = document.createElement('style');
document.head.appendChild(TrdsWorkmediaListStyle);
TrdsWorkmediaListStyle.textContent = `
    #workmedia-list .filter{
        gap: var(--space--xs);
        margin-bottom: var(--space--xs);
    }
    #workmedia-list .count{
        display: block;
        color: var(--color--secondary-text);
        font-style: italic;
        margin-bottom: var(--space--m);
    }
    #workmedia-list .more-work-button{
        margin-top: var(--space--l);
    }
`;

export default function WorkmediaList({ isShort }){

    const [currentCategory, setCurrentCategory] = useState('all');
    const [count, setCount] = useState(0);
    const { documents } = useCollection('workmedias', undefined, ['createdAt', 'desc']);
    const [workmedias, setWorkmedias] = useState([]);

    const handleCategoryChange = (e) => {
        setCurrentCategory(e.target.value);
    }

    useEffect(() => {

        if(documents){

            if(isShort){
                setWorkmedias([documents[0], documents[1], documents[2]])
            } else {
                setWorkmedias( documents.filter(document => {
                    switch(currentCategory) {
                        case 'all':
                            return true
                        case 'dent':
                        case 'hail':
                            return document.category.value === currentCategory
                        default:
                            return true
                    }
                }));
            }

        }

    }, [currentCategory, documents, isShort])

    useEffect(() => {
        setCount(workmedias.length);
    }, [workmedias])

    return(
        <>
            {!isShort && (
            <>
            <trds-stack class="filter">
                <label htmlFor='category-select'>Kategria</label>
                <select name='category-select' onChange={handleCategoryChange}>
                    <option value="all">Mind</option>
                    <option value="dent">Horpads</option>
                    <option value="hail">Jgkr</option>
                </select>
            </trds-stack>
            <span className="count">{count} tallat</span>
            </>
            )}
            <trds-grid class="grid boxes-layout">
                {workmedias.length === 0 && <p>Nincsenek Munkamdik!</p>}
                {workmedias.map(workmedia => (
                    <Workmedia key={workmedia.id} workmedia={workmedia} />
                ))}
            </trds-grid>
            {isShort && (
                <a is="trds-button--link" text="Tovbbi munkink" icon="solid/arrow-right" class="icon-on-right more-work-button trds-button" href="/munkaink" />
            )}
        </>
    )

}