import { useEffect, useState, useRef } from "react";
import { database } from "../firebase";
import { collection, onSnapshot, where, orderBy, query } from "firebase/firestore";

export const useCollection = (dbCollection, _query, _orderBy) => {

  const [documents, setDocuments] = useState(null)

  // if we don't use a ref --> infinite loop in useEffect
  // _query is an array and is "different" on every function call
  const queryRef = useRef(_query).current
  const orderByRef = useRef(_orderBy).current

  useEffect(() => {
    let ref = collection(database, dbCollection)

    if (queryRef) {
      ref = query(ref, where(...queryRef))
    }
    if (orderByRef) {
      ref = query(ref, orderBy(...orderByRef))
    }

    const unsubscribe = onSnapshot(ref, snapshot => {
      let results = []
      snapshot.docs.forEach(doc => {
        results.push({...doc.data(), id: doc.id})
      });
      
      // update state
      setDocuments(results)
    }, error => {
      console.log(error)
    })

    // unsubscribe on unmount
    return () => unsubscribe()

  }, [dbCollection, queryRef, orderByRef])

  return { documents }
}