import { useEffect, useRef } from "react";

let TrdsWorkmediaStyle = document.createElement('style');
TrdsWorkmediaStyle.textContent = `
    trds-workmedia{
        display: block;
        background-color: var(--color--secondary-bg);
        max-width: var(--element--max-width);
    }
    trds-workmedia_media{
        display: block;
        overflow: hidden;
        position: relative;
        background-color: black;
        padding-bottom: 56.25%;
    }
    trds-workmedia_media_before,
    trds-workmedia_media_after{
        display: block;
        transition: opacity .5s;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
    }
    trds-workmedia_media_after{
        z-index: 1;
        opacity: 0;
    }
    trds-workmedia_media video{
        height: 100% !important;
    }
    trds-workmedia[after-media-active] trds-workmedia_media_after{
        opacity: 1;
        z-index: 3;
    }
    trds-workmedia[after-media-active] trds-workmedia_media_before{
        opacity: 0;
    }
    trds-workmedia_body{
        display: block;
        box-sizing: border-box;
        padding: var(--space--l);
    }
    trds-workmedia_toggler{
        display: flex;
        gap: var(--space--s);
        margin-bottom: var(--space--l);
    }
    trds-workmedia_details{
        display: flex;
        flex-direction: column;
        gap: var(--space--m);
    }

}
`;
document.head.appendChild(TrdsWorkmediaStyle);

export default function Workmedia({ workmedia }){

    const self = useRef();
    const beforeToggler = useRef();
    const afterToggler = useRef();
    const beforeVideo = useRef();
    const afterVideo = useRef();

    useEffect(() => {
        afterToggler.current.classList.add('outline');
        if(beforeVideo.current) beforeVideo.current.muted = true;
        if(afterVideo.current) afterVideo.current.muted = true; 
    }, []);

    const handleBeforeToggle = () => {

        if(workmedia.afterMediaType !== 'image'){

            afterVideo.current.pause();
            afterVideo.current.currentTime = 0;

        }

        beforeToggler.current.classList.remove('outline');
        afterToggler.current.classList.add('outline');
        self.current.removeAttribute('after-media-active');

        if(workmedia.beforeMediaType !== 'image'){
            beforeVideo.current.play();
        }

    }

    const handleAfterToggle = () => {

        if(workmedia.beforeMediaType !== 'image'){

            beforeVideo.current.pause();
            beforeVideo.current.currentTime = 0;

        }

        beforeToggler.current.classList.add('outline');
        afterToggler.current.classList.remove('outline');
        self.current.setAttribute('after-media-active', '');

        if(workmedia.afterMediaType !== 'image'){
            afterVideo.current.play();
        }

    }

    return(
        <trds-workmedia ref={self}>
            <trds-workmedia_media>
                <trds-workmedia_media_before>
                    { workmedia.beforeMediaType === 'image' ?
                    <trds-image src={workmedia.beforeMediaUrl} alt="javts eltt" lazy></trds-image>
                    :
                    <video is="trds-video" preload="metadata" src={workmedia.beforeMediaUrl} ref={beforeVideo} controls playsInline></video>}
                </trds-workmedia_media_before>
                <trds-workmedia_media_after>
                { workmedia.afterMediaType === 'image' ?
                    <trds-image src={workmedia.afterMediaUrl} alt="javts utn" lazy></trds-image>
                    :
                    <video is="trds-video" preload="metadata" src={workmedia.afterMediaUrl} ref={afterVideo} controls playsInline></video>}
                </trds-workmedia_media_after>
            </trds-workmedia_media>
            <trds-workmedia_body>
                <trds-workmedia_toggler>
                    <button is="trds-button" ref={beforeToggler} onClick={()=> handleBeforeToggle()} text="Eltte"></button>
                    <button is="trds-button" ref={afterToggler} onClick={() => handleAfterToggle()} text="Utna"></button>
                </trds-workmedia_toggler>
                <trds-workmedia_details>
                    <div>
                        <h2 className="variant--1">Kategria</h2>
                        <span className="size--xl" style={{fontWeight: 'bold'}}>{workmedia.category.label}</span>
                    </div>
                    {workmedia.tags && workmedia.tags.length > 0 && (
                        <div>
                            <h2 className="variant--1" style={{marginBottom: 'var(--space--xs)'}}>Tagek</h2>
                            <trds-grid class="auto-width-layout" style={{gap: 'var(--space--xs)'}}>
                            { workmedia.tags.map(tag => (
                                <trds-tag key={tag}>{tag}</trds-tag>
                            ))}
                            </trds-grid>
                        </div>
                    )}
                    <div>
                        <h2 className="variant--1">Ltrehozva</h2>
                        <span className="size--s" style={{fontStyle: 'italic'}}>{workmedia.createdAt.toDate().toISOString().split('T')[0]}</span>
                    </div>
                </trds-workmedia_details>
            </trds-workmedia_body>
        </trds-workmedia>
    )

}